import React from "react";
import styled from "styled-components";

const StyledConvienceBox = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr 21px) 1fr;

  background: ${props => props.theme.white};
  box-shadow: 0 33px 83px -6.60241px rgba(67, 67, 67, 0.13);
  border-radius: 31px;

  padding: 34px 10px;
  flex: 1;
  
  @media(max-width: 700px) {  
    grid-template-rows: repeat(2, 1fr 21px) 1fr;
    grid-template-columns: 1fr;
    padding: 0 33px;
  }
`

const SeparatingLine = styled.div`
  background-color:  ${props => props.theme.lineColor};;
  margin-left: 10px;
  margin-right: 10px;
  min-height: 100px;

  @media(max-width: 700px) {
    margin: 10px 0;
    min-height: 0;
  }
`

export const ConvincingBox = ({children}) => {
    const items = React.Children.map(children, (child, index) =>
        <React.Fragment>
            {child}
            {index < children.length - 1 && <SeparatingLine/>}
        </React.Fragment>
    )

    return (
        <StyledConvienceBox>
            {items}
        </StyledConvienceBox>
    )
}