import {convincingProperties} from "./ConvincingProperties";
import {ConvincingItem} from "../../components/ConvincingBox/ConvincingItem";
import {OrangeStyledItem} from "./OrangeStyledItem";
import React from "react";
import {ConvincingBox} from "../../components/ConvincingBox/ConvincingBox";
import {ExtendedConvincingBox} from "../../components/ConvincingBox/ExtendedConvincingBox";

export const OrangeConvincingBox = () => {
    const items = convincingProperties.map((item, index) =>
        <ConvincingItem>
            <OrangeStyledItem>{React.createElement(item.icon)}</OrangeStyledItem>
            {item.title}
            {item.text}
        </ConvincingItem>
    )

    return (
        <ExtendedConvincingBox>
            {items}
        </ExtendedConvincingBox>
    )
}