import {LeftContainer} from "../../components/LeftContainer/LeftContainer";
import {ButtonBoxRow} from "../../components/LeftContainer/ButtonBoxRow";
import {UploadButton} from "./UploadButton";
import {RequirementsButton} from "./RequirementsButton";
import {AdvantagesList} from "../../components/LeftContainer/AdvantagesList";
import {OrangeLeftContainerProperties} from "./OrangeLeftContainerProperties";
import {AnchorPoint} from "../../components/AnchorPoint";
import {ReactComponent as ArrowIcon} from "../../icons/arrow.svg";
import {TitleText} from "../../components/LeftContainer/TitleText";

import styled from "styled-components";

const Arrow = styled(ArrowIcon)`
  position: absolute;
  width: ${props => props.theme.upperArrowWidth};
  fill: ${props => props.theme.white};
  transform: rotate(163.5deg);
  top: ${props => props.theme.upperArrowTop};
  left: ${props => props.theme.upperArrowLeft};
  z-index: -2;

  @media(max-width: ${props => props.theme.leftContainerChange}) {
    top: ${props => props.theme.upperArrowTop1300};
    left: ${props => props.theme.upperArrowLeft1300};
  }

  @media(max-width: 700px) {
    width: ${props => props.theme.upperArrowMobileWidth};
    top: ${props => props.theme.upperArrowTopMobile};
    left: ${props => props.theme.upperArrowLeftMobile};
  }
`

export const OrangeLeftContainer = ({customRef}) => {
    return (
        <LeftContainer customRef={customRef}>
            <TitleText>{OrangeLeftContainerProperties.title}</TitleText>
            <AnchorPoint>
                <Arrow/>
            </AnchorPoint>
            <AdvantagesList>
                {OrangeLeftContainerProperties.advantagesList}
            </AdvantagesList>
            <ButtonBoxRow>
                <UploadButton>{OrangeLeftContainerProperties.uploadButton}</UploadButton>
                <RequirementsButton>{OrangeLeftContainerProperties.requirementsButton}</RequirementsButton>
            </ButtonBoxRow>
        </LeftContainer>
    )
}