import React from "react";
import styled from "styled-components";
import {TextBase} from "../MainStyle";

const ButtonBase = ({className, children}) => {
    return (
        <div className={className}>
            <ButtonText> {children} </ButtonText>
        </div>
    )
}

const ButtonText = styled.p`
  ${TextBase};
  font-family: Avenir Medium, serif;
  font-size: 19px;
  line-height: 33px;
  
  @media(max-width: 700px) {
    font-size: 12.7239px;
    line-height: 22px;
  }
`

export const Button = styled(ButtonBase)`
  display: flex;
  align-items: center;
  justify-content: center;
  width: ${props => props.theme.buttonWidth};
  height: ${props => props.theme.buttonHeight};
  cursor: pointer;
  border-radius: 30px;

  @media(max-width: 700px) {
    flex: 1;
    height: ${props => props.theme.buttonMobileHeight};
    width: auto;
  }
`