import styled from "styled-components";

export const WebsiteContentBox = styled.div`
  margin-top: ${props => props.photoHeight + parseInt(props.theme.photographerTop) + 'px'};
  padding-left: ${props => props.theme.mainPadding};
  padding-right: ${props => props.theme.mainPadding};


  @media(max-width: ${props => props.theme.leftContainerChange}) {
    order: 2;
    margin-top: ${props => (props.photoHeight + parseInt(props.theme.photographerTopMobile)) + 'px'};

    padding-left: ${props => props.theme.mainMobilePadding};
    padding-right: ${props => props.theme.mainMobilePadding};
  }
`