import React from "react";
import styled from "styled-components";
import {TextBase} from "../../MainStyle";
import {ReactComponent as TickIcon} from "../../icons/tick.svg";

const Tick = styled(TickIcon)`
  width: ${props => props.theme.tickSize};
  margin-right: ${props => props.theme.tickRightMargin};
  fill: ${props => props.theme.tickColor};
  
  @media(max-width: 700px) {
    width: ${props => props.theme.tickMobileSize};
    margin-right: ${props => props.theme.tickMobileRightMargin};    
  }
`

const AdvantageText = styled.p`
  ${TextBase};
  font-family: Avenir Medium, sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 35px;
  
  @media(max-width: 700px) {
    font-weight: 500;
    font-size: 14.4413px;
    line-height: 32px;
  }
`

const StyledAdvantage = styled.div`
  display: flex;
  flex-direction: row;
`

const Advantage = ({children}) => {
    return (
        <StyledAdvantage>
            <Tick/>
            <AdvantageText>{children}</AdvantageText>
        </StyledAdvantage>
    )
}

const StyledAdvantagesList = styled.div`
  margin-bottom: ${props => props.theme.advantagesListMarginBottom};
  
  @media(max-width: 700px) {
    margin-bottom: ${props => props.theme.advantagesListMarginMobileBottom};
  }
`
export const AdvantagesList = ({children}) => {
    return (
        <StyledAdvantagesList>
            {children.map(text => <Advantage>{text}</Advantage>)}
        </StyledAdvantagesList>
    )
}