// import './Header.css'
import styled from "styled-components";
import {ReactComponent as LogoIcon} from "../icons/logo.svg";
import {ReactComponent as HamburgerIcon} from "../icons/hamburger.svg";
import {TextBase} from "../MainStyle";
import React from "react";

const Logo = styled(LogoIcon)`
  width: ${props => props.theme.logoWidth};
  flex-shrink: 0;
  
  @media(max-width: 700px) {
    width: ${props => props.theme.logoMobileWidth};
  }
 `

const Hamburger = styled(HamburgerIcon)`
  width: ${props => props.theme.hamburgerWidth};
  flex-shrink: 0;
  display: none;
  
  @media(max-width: ${props => props.theme.hideMenuWidth}) {
    display: flex;
  }
`

const MenuText = styled.p`
  ${TextBase};
  cursor: pointer;
  margin-left: 15px;
  margin-right: 15px;
  color: ${props => props.theme.paleBaseColor};

  font-weight: 800;
  font-size: 14px;
  line-height: 19px;
  
  &:hover {
    color: ${props => props.theme.hoverColor};
  }
`

const IconsContainer = styled.div`
  display: flex;
  justify-content: space-between;
  //background-color: green;
  
  @media(max-width: ${props => props.theme.hideMenuWidth}) {
    flex: 1;
  }
`

const MenuContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex: 1;
  
  @media(max-width: ${props => props.theme.hideMenuWidth}) {
    flex-direction: column;
    display: none;
  }
`

const BaseHeader = ({className, children}) => {

    return (
        <div className={className}>
            <IconsContainer>
                <Logo/>
                <Hamburger/>
            </IconsContainer>
            <MenuContainer>
                {children.map(option => <MenuText key={option}>{option}</MenuText>)}
            </MenuContainer>
        </div>
    )
}

export const Header = styled(BaseHeader)`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: ${props => props.theme.headerHeight};
  background-color: ${props => props.theme.headerBackground};
  //background-color: red;
  padding-left: ${props => props.theme.logoPadding};
  padding-right: ${props => props.theme.logoPadding};
  margin-bottom: ${props => props.theme.headerMarginBottom};
  
  @media(max-width: ${props => props.theme.hideMenuWidth}) {
    align-content: center;
  }

  @media(max-width: 700px) {
    height: ${props => props.theme.headerMobileHeight};
    padding-left: ${props => props.theme.logoPaddingMobile};
    padding-right: ${props => props.theme.logoPaddingMobile};
    margin-bottom: ${props => props.theme.headerMarginBottomMobile};
  }
`